<template>
<v-container grid-list-xs>
    <v-form>
        <v-card flat class="ma-2">
            <v-card-title>
                <v-spacer></v-spacer>
                <span class="subheading cairo">تسجيل الموظف</span>
                <v-spacer></v-spacer>
            </v-card-title>
            <v-card-text>
                <v-layout row wrap>
                    <v-flex
                    xs12
                    md6
                    >
                        <v-text-field
                            v-model="employeeName"
                            :rules="employeeNameRules"
                            :counter="20"
                            required
                            placeholder="اسم الموظف *"
                            class="ma-2"
                            @change="change"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md6
                    >
                        <v-text-field
                            v-model="employeePhone"
                            :rules="employeePhoneRules"
                            :counter="15"
