                            placeholder="رقم الجوال *"
                            class="ma-2 black--text"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md6
                    >
                    <v-text-field
                        v-model="employeeEmail"
                        :counter="30"
                        placeholder="البريد الالكترونيي *"
                        class="ma-2"
                    ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md6
                    >
                        <v-text-field
