                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md6
                    >
                        <v-select
                        v-model="select"
                        :items="employeeBranchs"
                        :rules="[v => !!v || 'الفرع مطلوبه']"
                        label="اسم الفرع *"
                        required
                        class="ma-2"
                        ></v-select>
                    </v-flex>
                    <v-flex
                    xs12
                    md6
                    >
                        <v-text-field
                            v-model="employeePassword"
                            :append-icon="showPassword ? 'visibility' : 'visibility_off'"
                            :rules="[rules.required, rules.min]"
                            :type="showPassword ? 'text' : 'password'"
                            name="employeePassword"
                            placeholder="كلمه المرور"
                            hint="على الاقل 6 حروف"
                            counter
                            @click:append="showPassword = !showPassword"
                        ></v-text-field>
                    </v-flex>
                </v-layout>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat color="success" @click="submit"><span class="cairo">تسجيل</span></v-btn>
                <v-btn flat color="red" @click="reset"><span class="cairo">مسح الحقول</span></v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-form>
</v-container>
</template>

<script>
export default {
