            ],
            employeePassword: '',
            select: null,
            employeeBranchs: [
                'فرع مكه ',
                'فرع المدينه ',
                'فرع جده ',
            ],
            password: 'Password',
            rules: {
                required: value => !!value || 'مطلوبه',
                min: v => v.length >= 6 || 'على الاقل 6 خانات',
            }
        }
    },
    methods: {
        submit () {
            // console.log([
            //     this.employeeName,
            //     this.employeePhone,
            //     this.employeeEmail,
            //     this.employeeJobeNumber,
            //     this.employeePassword,
            //     this.employeeBranchName
            // ])
        },
        change () {
            // console.log(this.employeeName)
        },
        reset () {
            // console.log('Reset Responser Info')
        }
    },
    computed: {
        employeeBranchName(){
            return this.select
        }
    }
}
</script>